import React, { Component } from 'react';
// import {BrowserRouter as Router, Route, Link} from 'react-router-dom'
import Button from "./Button";
import {withRouter} from 'react-router-dom';
class ArticleTeaser extends Component {
    // constructor(props){
    //     super(props);
    //     this.state = {}
    // }
    onClick = () => {
        this.props.history.push(this.props.href)
      }
      render(){

          return ( 
              
              <div className="articleTeaser">
                <img className="articleTeaser__img"src= {this.props.img} alt=""></img>
                <h1 className="articleTeaser__title">{this.props.title}</h1>
                <p className="articleTeaser__body">{this.props.body}</p>
                <button onClick = {this.onClick} className="articleTeaser__btn--readMore">{this.props.btn}
              </button>
           
            </div>
           
           );
        }
}

 
export default withRouter(ArticleTeaser);