import ArtHeader from '../ArtH';
import ArtImg from '../ArtI';
import ArtParagraph from '../ArtP';
import ArtLi from '../ArtList';
import React, { Component } from 'react';
class Art2 extends Component {
    state = { 
        title: 'LICZĄ SŁOWA I',
        title2: 'Na początku było słowo, a potem dystans',
        author: 'Iwo Gruca',
        date: '03 - 2019',
        // img: imgArt1,
        p1:'Matematyka posiada wiele zastosowań. Pierwsze, co przychodzi na myśl u większości z nas, to finanse, analiza danych (po staropolsku data science), bądź teoretyczna oraz praktyczna fizyka. Jednak okazuje się, że  „królowa nauk” pojawia się również w mniej spodziewanych miejscach. Ten krótki artykulik jest w zamierzeniu jednym z serii popularnonaukowych materiałów, w których postaram się opisać powiązania matematyki z humanistyką. Naszą pierwszą nauką humanistyczną będzie lingwistyka. Czy da się zmierzyć wiersz? Czy możemy przewidzieć odpowiedź rozmówcy na zadane przez nas pytanie (oczywiście zakładając, że nie jest ono retoryczne)? Czy możemy stworzyć szyfr nie do złamania? Te oraz podobne problemy stara się rozwiązać lingwistyka matematyczna. ',
        p2:'W tym artykule, chciałbym Wam opisać, czym zajmuję się dodatkowo w ramach swoich studiów. Niedawno, podczas pracy nad pewnym projektem, miałem styczność z problematyką podobieństwa słów. Pierwszym problemem (lub za lwowską szkołą matematyczną: problematem) jest kwestia „jak traktować słowo?”. Wbrew pozorom, stanowi to bardzo szeroki temat. O słowach można myśleć jako o skończonych ciągach znaków (tzw. diakrytów), ale nic nie stoi na przeszkodzie, żeby potraktować je jako wykresy wysokości dźwięku od czasu. Dla wygody przyjmiemy jednak tę pierwszą definicję (taką graficzną postać słowa występującą w słowniku nazywa się w lingwistyce leksemem). Kolejną przeszkodą stojącą na naszej drodze jest stwierdzenie co to znaczy, że słowa są blisko lub daleko od siebie. Dziedziną matematyki, w której kręgu zainteresowań znajduje się pojęcie odległości jest topologia. W języku tejże teorii odległość to nic innego jak wartość metryki – funkcji o specjalnych własnościach: ', 
        p3: 'LICZĄ SŁOWA metryki na sobie, żeby móc się zdystansować do kolejnego wpisu. W międzyczasie wszystkim zainteresowanych czym są leksemy, albo jak z pączka zrobić kubek, polecam lekturę Wstępu do językoznawstwa Renaty Grzegorczykowej oraz do Topologii ogólnej Ryszarda Engelkinga. Niech słowa będą z Wami! ',
        items:[
            {id:1, txt: 'd(x,y) = 0 wtedy i tylko wtedy, gdy  x = y (identyczność nierozróżnialnych elementów), '},
            {id:2, txt: 'd(x,y) = d(y,x)(symetria), '},
            {id:3, txt: 'd(x,z) ≤ d(x,y) + d(y,z)(nierówność trójkąta). '},
        ]
    
    }
    render() { 
        const {title,title2,img, author,date, p1,p2,p3,items} = this.state
        return (
            <div className="articlePage">
                <ArtHeader title = {title} title2 = {title2} author = {author} img = {img} date = {date}/>
                <ArtImg img = {img}/>
                <ArtParagraph p = {p1}/>
                <ArtParagraph p = {p2}/>
                <ul className="artPage__list">
                {items.map(item => <ArtLi key = {item.id} li = {item.txt}/>)}
                </ul>
                <ArtParagraph p = {p3}/>
              
            </div>
          );
    }
}
 
export default Art2;