import React, { Component } from 'react';
import partnerImg from './img/partnerImg.jpg'
import joinUsImg from './img/people.jpg'
import ArticleTeaser from './ArticleTeaser';
import {BrowserRouter as Router, Route, withRouter} from 'react-router-dom'


class Articles extends Component {
        state = { 
            articlesList: [
                {id: 1, img: partnerImg, title: 'Telepatia, czy “tylko” matematyczny umysł?', 
                body: 'Tak dobrze znane z filmów karciane sztuczki magiczne owiane są legendą; na granicy mitu i fascynacji. Zazwyczaj polegają na zwinnych ruchach magika, manipulacji widzem czy wykorzystaniu specjalnie przygotowanych rekwizytów. Jednak, w niektórych przypadkach, wystarczy tylko wprawne oko oraz matematyczny umysł.',
                href: '/art1'},
                {id: 2, img: joinUsImg, title: 'Liczą słowo', 
                body: 'Matematyka posiada wiele zastosowań. Pierwsze, co przychodzi na myśl u większości z nas, to finanse, analiza danych (po staropolsku data science), bądź teoretyczna oraz praktyczna fizyka. Jednak okazuje się, że  „królowa nauk” pojawia się również w mniej spodziewanych miejscach. Ten krótki artykulik jest w zamierzeniu jednym z serii popularnonaukowych materiałów, w których postaram się opisać powiązania matematyki z humanistyką. Naszą pierwszą nauką humanistyczną będzie lingwistyka. Czy da się zmierzyć wiersz? Czy możemy przewidzieć odpowiedź rozmówcy na zadane przez nas pytanie (oczywiście zakładając, że nie jest ono retoryczne)? Czy możemy stworzyć szyfr nie do złamania? Te oraz podobne problemy stara się rozwiązać lingwistyka matematyczna. ',
                href: '/art2'},
             
              ]
         }

    render() { 
        const articlesTeasers = this.state.articlesList.map(item => <ArticleTeaser 
                key = {item.id}
                // onclick = {this.handleOnClick}
                img = {item.img}
                title = {item.title}
                body = {item.body.substring(0,300) + ' ...'}
                btn = {'Czytaj'}
                href = {item.href}
                {...this.props}
                />)
        return (
           

            <div className="articlesSection">
                {articlesTeasers}
            </div>
          
         );
    }
}
 
export default withRouter(Articles);